@custom-media --wide-vp (width >= 1280px);
@custom-media --middle-vp (768px <= width <= 1279.9px);
@custom-media --narrow-vp (width <= 767.9px);
@custom-media --not-narrow-vp (width >= 767.9px);
@custom-media --not-wide-vp (width <= 1280px);
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src:
    url("../fonts/oswaldregular.woff2") format("woff2"),
    url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src:
    url("../fonts/oswaldmedium.woff2") format("woff2"),
    url("../fonts/oswaldmedium.woff") format("woff");
}
:root {
  --accent-font: "Oswald", "Impact", sans-serif;
  --main-font: "Arial", "Helvetica", sans-serif;
}
.visually-hidden {
  position: absolute;
  transform: scale(0);
}
.page {
  font-family: var(--main-font);
}
.page__body {
  --scrollbar: calc(100vw - 100%);
  margin: 0;
}
.page__centerer {
  position: relative;
  --content-indent: calc(20px + (10vw - 32px) / 4.48);

  margin-inline: var(--content-indent) calc(var(--content-indent) - var(--scrollbar));

  @media (--wide-vp) {
    box-sizing: border-box;
    --content-indent: auto;
    padding: 0 110px;
    max-width: 1440px;
    margin: 0 auto;
  }
}
.page__form-title {
  font-family: "Oswald", sans-serif;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  color: #000000;
  text-align: start;
  margin: 0;

  @media (--not-narrow-vp) {
    font-size: 60px;
    line-height: 60px;
    text-align: left;
  }
}
.page__form-title-container {
  display: flex;
  flex-direction: column;
  gap: 37px;
  box-sizing: border-box;
  padding: 27px  0 39px;

  @media (--narrow-vp) {
    border-top: 1px solid #d9d9d9;
  }

  @media (--middle-vp) {
    gap: 52px;
    padding: 75px 0 69px;
  }

  @media (--wide-vp) {
    gap: 54px;
    padding: 85px 110px 72px;
  }
}
.button {
  font-family: var(--accent-font);
  width: fit-content;

  &--size_wide {
    width: 100%;
  }
}
.header {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  box-sizing: border-box;
  align-items: center;

  @media (--narrow-vp) {
    background-color: var(--header-background-color-mobile);
  }

  @media (--not-narrow-vp) {
    display: flex;
    justify-content: space-between;
  }

  @media (--middle-vp) {
    padding: 27px 0 0;
  }

  @media (--wide-vp) {
    padding: 53px 0 0;
  }
}
.header__logo-container {
  z-index: 1;
  flex: 1 0 auto;

  @media (--narrow-vp) {
    width: 100%;
    padding: 10px 0 18px;
  }
}
.header__logo-link {
  display: grid;
  grid-template-columns: 1fr 1fr;
  box-sizing: border-box;
  align-items: center;
  flex-wrap: nowrap;
  text-decoration: none;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }

  @media (--middle-vp) {
    display: flex;
    justify-content: space-between;
    max-width: 174px;
  }

  @media (--wide-vp) {
    display: flex;
    justify-content: space-between;
    max-width: 202px;
  }
}
.header__logo-link::before {
  @media (--narrow-vp) {
    content: "";
    display: block;
    background-repeat: no-repeat;
    width: 34px;
    height: 38px;
    background-image: url("../img/logo-cat.svg");
  }

  @media (--middle-vp) {
    content: "";
    display: block;
    background-repeat: no-repeat;
    background-image: url("../img/cat-logo-tablet.svg");
    width: 174px;
    height: 50px;
  }

  @media (--wide-vp) {
    content: "";
    display: block;
    background-repeat: no-repeat;
    background-image: url("../img/cat-logo-desktop.svg");
    width: 202px;
    height: 59px;
  }
}
@media (--narrow-vp) {
  .header__logo-link::after {
    content: "";
    display: block;
    justify-self: flex-end;
    width: 101px;
    height: 18px;
    transform: translateX(50%);
    background-image: url("../img/logo-words.svg");
  }
}
.navigation {
  position: relative;
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0;

  @media (--not-narrow-vp) {
    display: block;
    justify-content: space-between;
  }

  @media (--middle-vp) {
    flex-wrap: wrap;
  }
}
.navigation--nojs {
  .navigation__menu-button {
    display: none;
  }
}
@media (--narrow-vp) {
  .navigation--closed {
    .navigation__menu-button::after {
      content: url("data:image/svg+xml,%3Csvg width='24' height='18' viewBox='0 0 24 18' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='%23000' stroke-width='1.5' d='M0 1.25h24M0 9.25h24M0 17.25h24'/%3E%3C/svg%3E");
      display: block;
      width: auto;
      height: auto;
      border: none;
      outline: none;
    }

    .navigation__list {
      display: none;
    }
  }


  .navigation--opened {
    .navigation__menu-button::after {
      content: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath transform='scale(.97098 1.0282) rotate(-45 24.51 7.237)' stroke='%23000' stroke-width='1.5' d='M0-.75h24.758'/%3E%3Cpath transform='matrix(.68659 .72705 -.68659 .72705 1 2)' stroke='%23000' stroke-width='1.5' d='M0-.75h24.758'/%3E%3C/svg%3E");
      display: block;
      width: auto;
      height: auto;
      border: none;
      outline: none;
    }

    .navigation__list {
      display: block;
    }
  }
}
.navigation__list {
  list-style: none;
  margin: 0;
  padding: 0;
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-direction: row;

  @media (--narrow-vp) {
    position: absolute;
    top: 100%;
    left: 0;
    transform: translateX(-50%);
    color: var(--navigation-text-color);
    width: 100vw;
    flex-direction: column;
    z-index: 2;
  }

  @media (--not-narrow-vp) {
    transform: none;
  }

  @media (--middle-vp) {
    min-width: 440px;
  }

  @media (--wide-vp) {
    min-width: 478px;
  }
}
.navigation__menu-button {
  position: absolute;
  box-sizing: border-box;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
  height: 18px;
  padding: 0;
  margin: 0;
  background-color: transparent;
  outline: none;
  border: none;

  @media (--middle-vp) {
    display: none;
  }

  @media (--wide-vp) {
    display: none;
  }
}
.navigation__item {
  box-sizing: border-box;
  background-color: #ffffff;
  border-top: 1px solid;
  border-color: #e6e6e6;
  color: inherit;

  @media (--wide-vp) {
    border-color: transparent;
    background-color: transparent;
  }

  @media (--middle-vp) {
    border-color: transparent;
    background-color: transparent;
  }
}
@media (--narrow-vp) {
  .navigation__item:last-child {
    border-bottom: 1px solid #e6e6e6;
  }
}
@media (--not-narrow-vp) {
  .navigation__item [aria-current="page"] {
    border-bottom: 2px solid var(--menu-line-color);
  }
}
.navigation__link {
  display: block;
  font-family: "Oswald", sans-serif;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  color: inherit;
  padding-top: 22px;
  padding-bottom: 24px;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  @media (--middle-vp) {
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    padding: 8px 0 8px;
  }

  @media (--wide-vp) {
    font-size: 20px;
    line-height: 30px;
    font-weight: 400;
    padding: 8px 0 8px;
  }
}
.promo {
  margin: 0 auto;
  max-width: 1440px;
}
.promo__structure {
  position: relative;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  text-align: center;
  padding-top: 27px;

  min-height: 480px;

  @media (--middle-vp) {
    min-height: 790px;
    padding-top: 40px;
  }

  @media (--wide-vp) {
    gap: 52px;
    text-align: left;
    color: #000000;
    height: 100%;
    min-height: 636px;
    padding-bottom: 82px;
  }
}
.promo__text-container {
  display: flex;
  flex-direction: column;
  gap: 25px;
  padding: 0 17px;
  z-index: 1;

  @media (--middle-vp) {
    gap: 41px;
  }

  @media (--wide-vp) {
    gap: 40px;
  }
}
.promo__title {
  font-family: "Oswald", sans-serif;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  color: #ffffff;
  margin: 0;

  @media (--middle-vp) {
    font-size: 60px;
    line-height: 60px;
    text-align: start;
    color: #000000;
  }

  @media (--wide-vp) {
    font-size: 60px;
    line-height: 60px;
    text-align: start;
    color: #000000;
  }
}
.promo__text {
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  text-transform: uppercase;
  color: #ffffff;
  margin: 0;

  @media (--middle-vp) {
    font-size: 20px;
    line-height: 20px;
    text-align: start;
    color: #000000;
  }

  @media (--wide-vp) {
    font-size: 20px;
    line-height: 20px;
    text-align: start;
    color: #000000;
  }
}
.promo__image-container {
  position: absolute;
  bottom: 40px;
  left: 50%;
  width: 280px;
  transform: translateX(-50%);
  z-index: 0;

  @media (--middle-vp) {
    width: 709px;
  }

  @media (--wide-vp) {
    width: 552px;
  }
}
.promo__image {
  display: block;
  width: 100%;
  height: 100%;
}
.promo__button {
  position: absolute;
  bottom: 0;
  display: block;
  box-sizing: border-box;
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  text-decoration: none;
  text-transform: uppercase;
  color: var(--button-text-color);
  width: 100%;
  padding: 7px;
  background-color: var(--button-color);

  @media (--middle-vp) {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    line-height: 20px;
    max-width: 244px;
    min-height: 54px;
    padding: 0;
    top: 270px;
    left: 120px;
    bottom: 100%;
  }

  @media (--wide-vp) {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    line-height: 20px;
    max-width: 244px;
    min-height: 54px;
    padding: 0 0;
  }
}
.footer {
  @media (--not-wide-vp) {
    background-color: var(--footer-background-color);
  }
}
.footer__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 236px;

  @media (--not-narrow-vp) {
    flex-direction: row;
    align-items: center;
    flex-wrap: nowrap;
    min-height: 147px;
  }

  @media (--wide-vp) {
    background-color: var(--footer-background-color);
  }
}
.footer__logo-container {
  align-self: center;
  padding: 39px 0 32px;

  @media (--not-narrow-vp) {
    flex-grow: 0;
  }
}
.nutrition-programm {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding-top: 20px;

  @media (--middle-vp) {
    padding-bottom: 49px;
  }

  @media (--wide-vp) {
    display: flex;
    flex-direction: row;
    gap: 80px;
    padding-bottom: 71px;
  }
}
.nutrition-programm__card {
  display: flex;
  flex-wrap: wrap;
  column-gap: 19px;
  row-gap: 22px;
  align-items: center;
  box-sizing: border-box;
  background-color: #f2f2f2;
  padding: 22px;

  @media (--middle-vp) {
    display: grid;
    grid-template-columns: 1fr 270px;
  }

  @media (--wide-vp) {
    max-width: 570px;
    margin: 0;
  }
}
.nutrition-programm__card--slim::before {
  content: url("../icons/sprite.svg#cat-slim");
  display: block;
  width: 50px;
  height: 53px;

  @media (--middle-vp) {
    grid-area: 1/2/3/3;
    justify-self: center;
    width: 200px;
    height: 211px;
  }

  @media (--wide-vp) {
    width: 100px;
    height: 106px;
  }
}
.nutrition-programm__card--fat::before {
  content: url("../icons/sprite.svg#cat-muscle");
  display: block;
  width: 67px;
  height: 50px;

  @media (--middle-vp) {
    grid-area: 1/2/3/3;
    justify-self: center;
    width: 268px;
    height: 200px;
  }

  @media (--wide-vp) {
    width: 134px;
    height: 100px;
  }
}
.nutrition-programm__title {
  font-family: "Oswald", sans-serif;
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  color: #000000;
  text-transform: uppercase;
  text-align: center;
  margin: 0;

  @media (--middle-vp) {
    font-size: 36px;
    line-height: 36px;
    text-align: start;
  }

  @media (--wide-vp) {
    font-size: 36px;
    line-height: 36px;
    text-align: start;
  }
}
.nutrition-programm__text {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: var(--base-font-color);
  margin: 0;

  @media (--middle-vp) {
    font-size: 16px;
    line-height: 24px;
    text-align: start;
  }

  @media (--wide-vp) {
    font-size: 16px;
    line-height: 24px;
    text-align: start;
  }
}
.nutrition-programm__link {
  display: block;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 16px;
  font-weight: 400;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
  border-top: 1px solid #d9d9d9;
  padding-top: 15px;

  @media (--middle-vp) {
    font-size: 20px;
    line-height: 30px;
    text-align: start;
  }

  @media (--wide-vp) {
    font-size: 20px;
    line-height: 30px;
    text-align: start;
  }
}
.nutrition-programm__link::after {
  content: url("data:image/svg+xml,%3Csvg width='24' height='12' viewBox='0 0 24 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cline y1='6' x2='18' y2='6' stroke='black' stroke-width='2'/%3E%3Cpath d='M24 6L17.25 11.1962L17.25 0.803848L24 6Z' fill='black'/%3E%3C/svg%3E%0A");
  display: inline-block;
  width: 24px;
  height: 12px;
  margin-left: 15px;

  @media (--middle-vp) {
    margin-left: 25px;
  }

  @media (--wide-vp) {
    margin-left: 28px;
  }
}
.nutrition-programm__link:hover::after {
  content: "";
  display: inline-block;
  background-image: url("../icons/sprite.svg#arrow-active");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 33px;
  height: 10px;
  margin-left: 15px;

  @media (--middle-vp) {
    margin-left: 25px;
  }

  @media (--wide-vp) {
    margin-left: 28px;
  }
}
.nutrition-programm__link:active::after {
  content: "";
  display: inline-block;
  background-image: url("../icons/sprite.svg#arrow-active");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 33px;
  height: 10px;
  margin-left: 15px;
  opacity: 0.3;

  @media (--middle-vp) {
    margin-left: 25px;
  }

  @media (--wide-vp) {
    margin-left: 28px;
  }
}
.description {
  padding-top: 20px;

  @media (--middle-vp) {
    padding-bottom: 60px;
    border-bottom: 1px  solid var(--line-after-description-color);
  }
}
.description__title {
  font-family: "Oswald", sans-serif;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  color: #000000;
  text-align: start;
  margin: 0;
}
@media (--not-narrow-vp) {
  .description__title {
    font-size: 60px;
    line-height: 60px;
  }
}
.description__item {
  position: relative;
  display: flex;
  align-items: center;
  gap: 20px;

  @media (--not-narrow-vp) {
    flex-direction: column;
    align-items: start;
    justify-content: center;
    min-height: 280px;
  }

  @media (--middle-vp) {
    max-width: 197px;
  }

  @media (--wide-vp) {
    max-width: 246px;
  }
}
.description__item::before {
  content: "";
  display: block;
  flex-shrink: 0;
  width: 60px;
  height: 60px;
  background-repeat: no-repeat;
  background-size: cover;

  @media (--not-narrow-vp) {
    width: 80px;
    height: 80px;
  }
}
.description__item--feed::before {
  background-image: url("../icons/sprite.svg#nutrients");
}
.description__item--powder::before {
  background-image: url("../icons/sprite.svg#powder");
}
.description__item--eating::before {
  background-image: url("../icons/sprite.svg#eating");
}
.description__item--timer::before {
  background-image: url("../icons/sprite.svg#timer");
}
.description__text {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  margin: 0;
  color: var(--base-font-color);

  @media (--middle-vp) {
    font-size: 16px;
    line-height: 24px;
  }
}
.description__list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  counter-reset: section;

  @media (--middle-vp) {
    flex-direction: row;
    flex-wrap: wrap;
    column-gap: 137px;
    row-gap: 90px;
  }

  @media (--wide-vp) {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
@media (--not-narrow-vp) {
  .description__item::after {
    counter-increment: section;
    content: counter(section);
    position: absolute;
    z-index: -1;
    right: 0;
    bottom: 0;
    font-family: "Oswald", sans-serif;
    font-size: 280px;
    line-height: 280px;
    font-weight: 400;
    color: var(--description-numders-color);
  }
}
.example {
  position: relative;
  padding: 47px 0;

  @media (--wide-vp) {
    padding-bottom: 0;
  }
}
.example::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: calc(100% - 47px);

  @media (--not-wide-vp) {
    background-color: var(--example-background-color);
  }

  @media (--not-narrow-vp) {
    height: 462px;
  }
}
.example__container::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;

  @media (--wide-vp) {
    background-color: var(--example-background-color);
  }

  @media (--not-narrow-vp) {
    height: 462px;
  }
}
.example__container {
  display: flex;
  flex-direction: column;

  @media (--middle-vp) {
    gap: 70px;
    align-items: center;
  }

  @media (--wide-vp) {
    flex-direction: row;
    justify-content: space-between;
    padding-top: 70px;
    padding-bottom: 65px;
  }
}
.example__facts {
  display: flex;
  flex-direction: column;
  gap: 20px;

  @media (--middle-vp) {
    gap: 54px;
  }

  @media (--wide-vp) {
    justify-content: space-between;
    max-width: 436px;
  }
}
.example__title {
  font-family: "Oswald", serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: #000000;
  padding-bottom: 20px;
  margin: 0;
}
.example__text {
  font-family: "Arial", serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: var(--base-font-color);
  margin: 0;

  @media (--not-narrow-vp) {
    font-size: 16px;
    line-height: 24px;
  }

  @media (--wide-vp) {
    flex-grow: 1;
  }
}
.example__facts-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 34px;
  margin: 0;

  @media (--middle-vp) {
    display: flex;
    column-gap: 42px;
    row-gap: 9px;
  }

  @media (--wide-vp) {
    max-width: 366px;
  }
}
.example__item-container {
