  }
}
.product-card__items {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.product-card__item-value,
.product-card__item-name {
  @media (--middle-vp) {
    font-size: 16px;
    padding: 9px 0;
    border-bottom: 1px solid var(--product-card__item-boreder-color);
  }

  @media (--wide-vp) {
    font-size: 16px;
    padding: 9px 0;
    border-bottom: 1px solid var(--product-card__item-boreder-color);
  }
}
.product-card__text {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 18px;
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: var(--base-font-color);

  @media (--middle-vp) {
    gap: 23px;
  }
}
.product-card__button {
  grid-column: 1/-1;
  box-sizing: border-box;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: var(--button-text-color);
  text-decoration: none;
  text-align: center;
  align-self: end;
  min-height: 40px;
  width: 100%;
  background-color: var(--button-color);
  padding: 10px;
}
.product-card__item-value {
  text-align: right;
}
.product-card__title {
  grid-column: 1/-1;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-align: left;
  text-transform: uppercase;
  color: #222222;

  @media (--narrow-vp) {
    padding-right: 60px;
  }

  @media (--middle-vp) {
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    color: #222222;
    padding: 0 40px;
  }

  @media (--wide-vp) {
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    color: #222222;
    padding: 0 10px;
  }
}
.product-card__image {
  display: block;
  width: 86px;
  height: 100%;
  object-fit: contain;
  justify-self: center;

  @media (--middle-vp) {
    position: absolute;
    left: 50%;
    bottom: 303px;
    transform: translateX(-50%);
    width: 168px;
    height: 200px;
  }

  @media (--wide-vp) {
    position: absolute;
    left: 50%;
    bottom: 314px;
    transform: translateX(-50%);
    width: 172px;
    height: 200px;
  }
}
.inputs-group {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 13px;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  border: none;

  @media (--not-narrow-vp) {
    gap: 20px;
  }
}
.inputs-group.inputs-group--type_cat {
  padding-bottom: 27px;

  @media (--not-narrow-vp) {
    border: none;
  }

  @media (--wide-vp) {
    grid-column: 1/2;
    padding-bottom: 0;
  }
}
.inputs-group.inputs-group--type_program {
  position: relative;
  padding-top: 23px;
  padding-bottom: 20px;

  @media (--not-narrow-vp) {
    border: 1px solid var(--input-border-color);
    padding: 25px 28px 25px;
  }

  @media (--middle-vp) {
    grid-column: 1/-1;
  }

  @media (--wide-vp) {
    grid-column: 2/3;
  }
}
.inputs-group.inputs-group--type_contacts {
  display: flex;
  flex-direction: column;

  @media (--middle-vp) {
    border: none;
  }

  @media (--wide-vp) {
    grid-column: 1/-1;
  }
}
.inputs-group--type_contacts > .inputs-group__group-title {
  @media (--wide-vp) {
    grid-column: 1/-1;
  }
}
.inputs-group--contacts-container {
  padding-top: 19px;

  @media (--middle-vp) {
    padding-top: 50px;
  }

  @media (--wide-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-top: 53px;
  }
}
.inputs-group.inputs-group--type_additionals {
  gap: 32px;
  padding-top: 56px;

  @media (--not-narrow-vp) {
    grid-column: 1/-1;
    gap: 53px;
    border: transparent;
  }

  @media (--middle-vp) {
    padding-top: 89px;
  }

  @media (--wide-vp) {
    padding-top: 93px;
  }
}
.inputs-group__checkbox-container {
  display: flex;
  flex-direction: column;
  gap: 13px;

  @media (--middle-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  @media (--wide-vp) {
    flex-direction: row;
  }
}
