.inputs-group__input-label {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  gap: 13px;
  flex-shrink: 0;
  flex-grow: 1;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  text-transform: uppercase;
  color: var(--base-font-color);
  white-space: nowrap;

  @media (--middle-vp) {
    font-size: 20px;
    line-height: 30px;
  }
}
.inputs-group__input {
  flex-grow: 1;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: var(--base-font-color);
  text-transform: uppercase;
  min-height: 46px;
  outline: none;
  border: 1px solid var(--input-border-color);
  padding: 0 15px;
  overflow: hidden;
  appearance: none;

  @media (--middle-vp) {
    font-size: 20px;
    line-height: 30px;
  }
}
.inputs-group--comment-label {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  text-align: start;
  margin: 0;
  gap: 34px;
  padding-bottom: 26px;
  padding-top: 28px;

  @media (--middle-vp) {
    gap: 70px;
    padding-bottom: 69px;
    padding-top: 69px;
  }

  @media (--wide-vp) {
    gap: 71px;
    padding-bottom: 71px;
    padding-top: 73px;
    grid-column: 1/-1;
  }
}
.inputs-group__group-title {
  --title-indent: 20px;
  position: relative;
  font-family: "Oswald", sans-serif;
  font-size: 24px;
  line-height: 26px;
  font-weight: 400;
  text-align: left;
  width: fit-content;
  margin: 0;
  padding: 0;

  @media (--narrow-vp) {
    max-width: 190px;
  }

  @media (--wide-vp) {
    grid-column: 1/-1;
  }
}
.inputs-group__group-title::before {
  content: "";
  position: absolute;
  top: 50%;
  right: calc(100% + var(--title-indent));
  display: block;
  width: calc(100vw - 100% - var(--title-indent));
  height: 2px;
  background-color: var(--line-color);
  box-shadow: calc(100vw + var(--title-indent)) 0 0 0 var(--line-color);

  @media (--narrow-vp) {
    background-color: transparent;
  }

  @media (--middle-vp) {
    --title-indent: 39px;
  }

  @media (--wide-vp) {
    --title-indent: 40px;
  }
}
.inputs-group__input::-webkit-outer-spin-button,
.inputs-group__input::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}
.inputs-group__input-radio:checked + .inputs-group__input-label::before {
  content: "";
  display: block;
  width: 30px;
  height: 30px;
  background: url("data:image/svg+xml,%3Csvg width='14' height='15' viewBox='0 0 14 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cellipse cx='7' cy='7.274' rx='6.75' ry='6.773' fill='%2368B738'/%3E%3C/svg%3E") no-repeat 50% 50%;
  box-shadow: inset 0 0 0 1px var(--mark-border-color);
}
.inputs-group__input-radio ~ .inputs-group__input-label::before {
  content: "";
  display: block;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  box-shadow: inset 0 0 0 1px var(--mark-border-color);
}
.inputs-group__input-checkbox:checked + .inputs-group__input-label::before {
  content: "";
  display: block;
  width: 30px;
  height: 30px;
  background: url("data:image/svg+xml,%3Csvg width='19' height='15' viewBox='0 0 19 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7 11.718 1.3 5.27.25 6.457 7 14.094 18.25 1.366 17.2.094 7 11.718Z' fill='%2368B738'/%3E%3C/svg%3E") no-repeat 50% 50%;
  box-shadow: inset 0 0 0 1px var(--mark-border-color);
}
.inputs-group__input-checkbox ~ .inputs-group__input-label::before {
  content: "";
  display: block;
  width: 30px;
  height: 30px;
  box-shadow: inset 0 0 0 1px var(--mark-border-color);
}
:root {
  --header-background-color-mobile: #ffffff;
  --header-background-color: transparent;
  --base-font-color: #444444;
  --button-color: #68b738;
  --line-color: #68b738;
  --button-text-color: #ffffff;
  --navigation-text-color: #000000;
  --input-border-color: #e7e7e7;
  --show-more-base-color: #f2f2f2;
  --line-after-description-color: #e6e6e6;
  --fact-border-color: #cdcdcd;
  --example-background-color: #eaeaea;
  --description-numders-color: #f2f2f2;
