  @media (--middle-vp) {
    width: 129px;
    flex-grow: 0;
    border: transparent;
    order: 0;
  }

  @media (--wide-vp) {
    width: 129px;
    flex-grow: 0;
    border: transparent;
    order: 0;
  }
}
.social__link {
  display: block;
  text-decoration: none;
}
.social__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.social__link--vk {
  width: 23px;
  height: 16px;

  @media (--not-narrow-vp) {
    width: 30px;
    height: 32px;
  }
}
.social__link--instagram {
  width: 16px;
  height: 16px;

  @media (--not-narrow-vp) {
    width: 23px;
    height: 23px;
  }
}
.social__link--facebook {
